import './index.css'
import { Pie, Bar, Scatter, HorizontalBar, Line } from 'react-chartjs-2'
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';

const bardata = {
    labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July'],
    datasets: [
        {
            label: 'My First dataset',
            backgroundColor: 'rgba(255,99,132,0.2)',
            borderColor: 'rgba(255,99,132,1)',
            borderWidth: 1,
            hoverBackgroundColor: 'rgba(255,99,132,0.4)',
            hoverBorderColor: 'rgba(255,99,132,1)',
            data: [65, 59, 80, 81, 56, 55, 40]
        }
    ]
};

const scatterData = {
    labels: ['Scatter'],
    datasets: [
        {
            label: 'My First dataset',
            fill: false,
            backgroundColor: 'rgba(75,192,192,0.4)',
            pointBorderColor: 'rgba(75,192,192,1)',
            pointBackgroundColor: '#fff',
            pointBorderWidth: 1,
            pointHoverRadius: 5,
            pointHoverBackgroundColor: 'rgba(75,192,192,1)',
            pointHoverBorderColor: 'rgba(220,220,220,1)',
            pointHoverBorderWidth: 2,
            pointRadius: 1,
            pointHitRadius: 10,
            data: [
                { x: 65, y: 75 },
                { x: 59, y: 49 },
                { x: 80, y: 90 },
                { x: 81, y: 29 },
                { x: 56, y: 36 },
                { x: 55, y: 25 },
                { x: 40, y: 18 },
            ]
        }
    ]
};

class Body extends Component {
    constructor(props) {
        super(props)

        this.state = {
            sortBy: 'MONTH',
            mainMarginLeft: 0
        }
    }

    _renderGraphs = () => {
        return <Fragment>
            <div className="row">
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            COST PRODUCED
                                </div>
                        <div className="card-body">
                            <HorizontalBar height={200}
                                data={{
                                    labels: ['Eleectricity', 'Gas'],
                                    datasets: [{
                                        data: [300, 50],
                                        backgroundColor: [
                                            '#FFCE56',
                                            '#36A2EB',
                                        ],
                                        hoverBackgroundColor: [
                                            '#FFCE56',
                                            '#36A2EB',
                                        ]
                                    }]
                                }}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            CHANGE IN COST
                                </div>
                        <div className="card-body">
                            <Bar
                                height={200}
                                data={bardata}
                                options={{
                                    maintainAspectRatio: false
                                }}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            USAGE ESTIMATE
                                </div>
                        <div className="card-body">
                            <Scatter height={200} data={scatterData} />
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            ENERGY INTENSITY
                                </div>
                        <div className="card-body">
                            <Pie height={200}
                                data={{
                                    labels: ['Eleectricity', 'Gas'],
                                    datasets: [{
                                        data: [300, 50],
                                        backgroundColor: [
                                            '#2cac72',
                                            '#36A2EB',
                                        ],
                                        hoverBackgroundColor: [
                                            '#2cac72',
                                            '#36A2EB',
                                        ]
                                    }]
                                }}
                            />
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            USAGE ESTIMATE
                                </div>
                        <div className="card-body">
                            <Scatter height={200} data={scatterData} />
                        </div>
                    </div>
                </div>
                <div className="col-lg-4 col-md-6 col-sm-6 col-xs-12">
                    <div className="card">
                        <div className="card-header">
                            CHANGE IN COST
                                </div>
                        <div className="card-body">
                            <Line
                                height={200}
                                data={bardata}
                                options={{
                                    maintainAspectRatio: false
                                }}
                            />
                        </div>
                    </div>
                </div>

            </div>
        </Fragment>
    }

    _menuOpen = () => {
        this.props.onMenuToggle()
        this.setState({ mainMarginLeft: this.state.mainMarginLeft == 0 ? 200 : 0 })
    }

    render() {
        return <div className='main' style={{ marginLeft: this.state.mainMarginLeft }}>
            <div className="header_">
                <div style={{ display: 'flex', flexDirection: 'row' }}>
                    <div className="title_"><i onClick={() => this._menuOpen()} style={{ backgroundColor: 'rgba(0,0,0,0.6)', padding: 8, borderRadius: 6 }} class={this.state.mainMarginLeft == 0 ? "fas fa-bars" : "fas fa-times"}></i>    Energy Dashboard</div>
                    <div style={{ color: 'white', fontSize: 12, marginTop: 40, marginLeft: 'auto', marginRight: 20 }}><i style={{ fontSize: 14 }} class="fas fa-sign-out-alt"></i> SIGNOUT</div>
                </div>
                <div className='itemGroup'>
                    <span onClick={() => this.setState({ sortBy: "TODAY" })} className="items_" style={this.state.sortBy == "TODAY" ? { backgroundColor: 'rgba(0,0,0,0.5)' } : {}}>TODAY</span>
                    <span onClick={() => this.setState({ sortBy: "MONTH" })} className="items_" style={this.state.sortBy == "MONTH" ? { backgroundColor: 'rgba(0,0,0,0.5)' } : {}}>MONTH</span>
                    <span onClick={() => this.setState({ sortBy: "YEAR" })} className="items_" style={this.state.sortBy == "YEAR" ? { backgroundColor: 'rgba(0,0,0,0.5)' } : {}}>YEAR</span>
                </div>
                <div className="container">
                    {this._renderGraphs()}
                </div>
            </div>
        </div>
    }
}

export default connect(
    state => (state),
    dispatch => ({onMenuToggle:() => dispatch({'type':'MENU_TOGGLE'})}) )
    (Body)