import React, { Component, Fragment } from 'react';
import './index.css'
import { connect } from 'react-redux'

var MenuItems = ['Dashboard', 'Cost', 'Appliances', 'Usage', 'Emissions']

class MenuDrawer extends Component {

  constructor(props) {
    super(props)
  }

  render() {
    return <Fragment>
      <div className="navigation" style={{ left: this.props.menuToggle ? 0 : -200 }}>
        <div className="header">
          <div className="title">FUSiON SMART</div>
          <div className="version">V2.2</div>
        </div>
        <ul className="menu">
          {MenuItems.map(item => <Fragment>
            <li><i class={item == MenuItems[0] ? "fas fa-tachometer-alt" : item == MenuItems[1] ? 'fas fa-dollar-sign' : item == MenuItems[2] ? 'fab fa-medapps' : item == MenuItems[3] ? "fas fa-sign-language" : "fab fa-affiliatetheme"}></i>{item}</li>
          </Fragment>)}
        </ul>
      </div>
    </Fragment>
  }
}

const mapStateToProps = (state) => {
  return state;
}

const mapActionsToProps = {
  
}

export default connect(mapStateToProps, mapActionsToProps)(MenuDrawer)
